import { Component, OnInit, ElementRef } from '@angular/core';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpClientModule,HttpClient } from '@angular/common/http';
import { LocalStorageService } from 'core/service/local-storage.service';
import { environment } from '../environments/environment';
import { Constants } from 'core/constants';
import { Router } from '@angular/router';
import { UserSession } from 'core/sessions';


declare var $: any;
declare var Swiper:any;
declare var Intercom:any;
declare var CryptoJS:any;


@Component({
  selector: 'app-explorepage',
  templateUrl: './explorepage.component.html',
  styleUrls: ['./explorepage.component.css',]
})

export class ExplorepageComponent implements OnInit, HttpSuccesFailureResponse {
  userid:any;
  userName:any;
  bannerData:any;
  tryP5MData:any;
  customerSpeaksData:any;
  priceModelData:any;
  categoryData:any;
  gymListData:any;
  trainerListData:any;
  topRatedClassesData:any;
  contactData:any;

  activitiesMaxItemNumber = 0;
  
  classListisLaoding=true;
  showAll=false;
  showAllPlaylist=false;
  lessCategoryData=[];
  allCategoryData=[];
  itemToScrollTo: any;
  youtubePlaylistData:any={};
  currency:any;
  public gymName = UserSession.getDynamicElements(Constants.GymName)

  constructor(private router: Router,private http_user: HttpUserAuthUtilsService,public http: HttpClient)
  {
    
  }

  // isLoggedIn() {
  //   return UserSession.isLoggedIn();
  // }


  ngViewAfterInit(){
    
  }

  ngOnInit() {
    this.currency = LocalStorageService.getCurrencyCode();
    this.userid = LocalStorageService.getUserId();
    this.userName = LocalStorageService.getUser().data.firstName;
    this.http_user.explorePage(this.userid,this,true);  
  }

  callYoutubeAPI(){
   let url = "https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&playlistId=".concat(Constants.youtubePlaylistId + "&key=" + Constants.youtubekey )
    this.http.get(url).subscribe(response=>{ 
   //   this.youtubePlaylistData = response.items; 
   let youtuberesponse:any = response;
   console.log(youtuberesponse)
   this.youtubePlaylistData = youtuberesponse;
   });
  }
  
  getAllSectionDetails(data) {

    data.forEach(element => {
      switch(element.viewType) {
        case 'BANNER_CAROUSEL_VIEW': this.getBannerDetails(element);
        break;
        case 'P5M_BUTTONS': this.getTryP5MDetails(element);
        break;
        case 'TEXT_CAROUSAL_VIEW': this.getCustomerSpeaksDetails(element);
        break;
        case 'PRICE_MODEL_CAROUSEL_VIEW': this.getPriceModelDetails(element);
        break;
        case 'CATEGORY_CAROUSEL_VIEW':
          this.getCategoryDetails(element);
        break;
        case 'GYM_VIEW':
          this.getGymListDetails(element);
        break;
        case 'TRAINER_VIEW':
          this.getTrainerListDetails(element);
        break;
        case 'TOP_RATED_CLASSES':this.getTopRatedClassesDetails(element);
        break;
        case 'CONFUSED_BUTTONS': this.getContactDetails(element);
        break;
        default:
      }
    });
  }

  getBannerDetails(data) {
    this.bannerData=data;
    }

  getTryP5MDetails(data){
    this.tryP5MData=data;
  }

  getCustomerSpeaksDetails(data) { 
    this.customerSpeaksData = data;
  }

  getPriceModelDetails(data) {
    this.priceModelData=data;
  }

  getCategoryDetails(data) {
  
   this.categoryData=data;
   data.data.forEach(element => {
     if(this.lessCategoryData.length<4)
      this.lessCategoryData.push(element)
  });
  data.data.forEach(element => {
     this.allCategoryData.push(element)
 });
 this.showAll=!this.showAll;

 setTimeout(() => {
  this.loadmoress(true);

 }, 1000);
 
  }
  
  onImgError(event) { 
    event.target.src  = '/assets/images/280X140_gym.jpg';
}
  getGymListDetails(data) {
    this.gymListData=data;
  }

  getTrainerListDetails(data) {
    this.trainerListData=data;
  }

  getTopRatedClassesDetails(data){
    this.topRatedClassesData=data;
  }

  getContactDetails(data){
    this.contactData=data;
  }

  hideIntercom() 
  { Intercom('shutdown');  }

  initializeAndOpenIntercom(){
    if (UserSession.isLoggedIn()) {
    var hash = CryptoJS.HmacSHA256(LocalStorageService.getUser().data.email, "N6GEqX94QBXoeYJKgh8RjfL6qjsTCDaHGL9BFg0t");   
    var hashHex = hash.toString(CryptoJS.enc.Hex);
    var user = LocalStorageService.getUser().data;
   Intercom("boot", {
      app_id: environment.intercom_app_id,
     name: LocalStorageService.getUser().data.firstName+" "+LocalStorageService.getUser().data.lastName, // Full name
      email: LocalStorageService.getUser().data.email, // Email address
      user_hash:hashHex,
      created_at:new Date().toLocaleDateString() , // Signup date
      plan_name: "basic", // Strings
      avatar: {
        "type": "avatar", 
        "image_url" :user.profileImageThumbnail
      }
    });
    Intercom('show');
  }
  else{
    Intercom("boot", {
      app_id: "qp091xcl",
      name : "Guest "+Math.floor(100000 + Math.random() * 900000)

    });
  }
  }


  onSuccess(type: any, responsedata: any) {
    if(type== Constants.explorePageApi) {
        this.getAllSectionDetails(responsedata.data); 
        this.callYoutubeAPI(); // called here so that the playlist isn't visible before the explore API response is received

      setTimeout(() => {
        this.setSliderParameters();
        this.classListisLaoding=false;

      }, 10);
      setTimeout(() => {

      }, 100);

    }
  }

  onFailure(type: any, response: any) {
    if(type== Constants.explorePageApi) { 
      this.classListisLaoding=false;

    }
  }

  navigateTo(type,obj?){
    if(type=='allTrainersPage') { this.router.navigate(['/trainers']); }
    if(type=='allGymsPage') { this.router.navigate(['/gyms']); }
    if(type=='classesCategory') {
      let filter='/classes?type=activity&'+'id='+obj.id;
      this.router.navigate(['/classes'], { queryParams: { type: 'activity', 'id': obj.id} });
    }
      if(type=='classesType') {
        this.router.navigate(['/classes'], { queryParams: { type: 'priceModel', 'id': obj.value} });
        }
    if(type=='settingsPage') { this.router.navigate(['/membership']) }
    if(type == 'classprofile'){

      this.router.navigate(['/classes/'+obj.classSessionId+'/'+obj.title.replace(/ /g, '')])

    }
    if(type == 'gymrofile'){
      
            this.router.navigate(['/gym/'+obj.gymId+'/'+obj.studioName.replace(/ /g, '')])
      
          }
    if(type == 'trainerprofile'){
        this.router.navigate(['/trainer/'+obj.trainerId+'/'+obj.trainerName.replace(/ /g, '')])
           }    
    
    if(type == 'youtube'){
      let link = "https://www.youtube.com/watch?v=".concat(obj.videoId);
      window.open(link, "_blank");
    }
  }
  redirect(url){
    if(url.trim().length>0){
     if (url.indexOf("/classes/")>-1 || url.indexOf("/share/classes/")>-1) {
        this.router.navigate(['/classes']);  
    } else if (url.indexOf("/share/gym/")>-1 || url.indexOf("/gym/")>-1) {
        let gymId = null;
        let stringlist = url.split("/gym/");
        if (stringlist != null && stringlist.length > 1) {
            gymId = stringlist[1].split("/")[0];
        }
        try {
          this.router.navigate(['/gym/'+gymId+'/'+gymId.replace(/ /g, '')])
         } catch (e) {
          this.router.navigate(['/classes']);  


        }

    } else if (url.indexOf("/share/trainer/")>-1 || url.indexOf("/trainer/")>-1) {
        let trainerId = null;
        let stringlist = url.split("/trainer/");
        if (stringlist != null && stringlist.length > 1) {
            trainerId = stringlist[1].split("/")[0];
        }
        try {
          this.router.navigate(['/trainer/'+trainerId+'/'+trainerId.replace(/ /g, '')])
         } catch (e) {
          this.router.navigate(['/classes']);  


        }
    } else if (url.indexOf("/classes")>-1) {
      this.router.navigate(['/classes']);  


    } else if (url.indexOf("/trainers")>-1) {
this.router.navigate(['/trainers']);  


    } else if (url.indexOf("/userschedule")>-1) {
       this.router.navigate(['/userschedule']);  
} else if (url.indexOf("/profile?type=favTrainer")>-1) {
  this.router.navigate(['/profile'], { queryParams: { type: 'favTrainer'} });



    } else if (url.indexOf("/profile?type=finishedClasses")>-1) {
      this.router.navigate(['/profile'], { queryParams: { type: 'finishedClasses'} });



    } else if (url.indexOf("/profile")>-1) {
      this.router.navigate(['/profile']);
 } else if (url.indexOf("/editprofile")>-1) {
  this.router.navigate(['/editprofile']);
 } else if (url.indexOf("/membership/membership")>-1) {
  this.router.navigate(['/membership/membership']);
 } else if (url.indexOf("/membership/transaction")>-1) {
  this.router.navigate(['/membership/transaction']);
 } else if (url.indexOf("/membership/notification")>-1) {
  this.router.navigate(['/membership/notification']);
 } else if (url.indexOf("/membership/aboutus")>-1 ||
            url.indexOf("/aboutus")>-1) {
              this.router.navigate(['/membership/aboutus']);

    } else if (url.indexOf("/membership/notifications")>-1) {
      this.router.navigate(['/membership/notifications']);

    } else if (url.indexOf("/membership")>-1) {
      this.router.navigate(['/membership']);
    } 
    
    }

  }

  setSliderParameters() {
   
    $('.topSliders').slick({
      dots: true,
      infinite: true,
      speed: 900,      
      slidesToShow: 1,
      autoplay:true,
      adaptiveHeight: true,
      arrows:false,
      
    });

    $('.whatCanWeHelpYou').slick({
      dots: true,
      infinite: false,
      speed: 300,      
      slidesToShow: 4,  
      
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      
      ]
      
    });

    // $('#slider').cardSlider({
    //   slideTag: 'div', 
    //   slideClass: 'slide',
    //   current: 2
    // })

    $('.testimonialsWithExplore').slick({
      dots: true,
      infinite: false,
      speed: 300,      
      slidesToShow: 1,      
    });

    $('.explore_ourGym').slick({
      dots: false,
      infinite: false,
      speed: 300,      
      slidesToShow:5,  
      
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1
          }
        }
      ]

    });

    $('.trc').slick({
      dots: true,    
      infinite: false,
      speed: 300,      
      slidesToShow:4,   
      
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
      
    });


  }
scroll(id){
  let el = document.getElementById(id);
  el.scrollIntoView({ behavior: 'smooth' });
}

  loadmoress(blockscroll?){  

       this.showAll=!this.showAll;
      if(!this.showAll){
        this.categoryData.data=[];
         this.lessCategoryData.forEach(element => {
          this.categoryData.data.push(element)
         });
       }else{
        this.categoryData.data=[];
         this.allCategoryData.forEach(element => {
          this.categoryData.data.push(element)
         });
       }
       if(!blockscroll)
       this.scroll('activity_section');
  }

  openIntercom(){
    Intercom('showNewMessage', "Hi")
  }

}





